import { css, CSSResult, unsafeCSS } from 'lit-element/lib/css-tag';

export type CssTextAlignment = 'left' | 'right' | 'centered' | 'justified';
export type CssTextOverflow = 'clip' | 'ellipsis';

export function getCssTextAlignment(alignment: CssTextAlignment): CSSResult {
    return css`${unsafeCSS(alignment)}`;
}

export function getCssTextOverflow(alignment: CssTextOverflow): CSSResult {
    return css`${unsafeCSS(alignment)}`;
}
